import React from 'react';
import '../assets/scss/main.scss';
import Helmet from 'react-helmet';
import { navigateTo } from 'gatsby-link';

import Header from '../components/Header';
import Main from '../components/Main';
import Footer from '../components/Footer';
import favicon from '../static/favicon.ico';

class Template extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isArticleVisible: false,
      timeout: false,
      articleTimeout: false,
      article: '',
      loading: 'is-loading',
    }
    this.handleOpenArticle = this.handleOpenArticle.bind(this);
    this.handleCloseArticle = this.handleCloseArticle.bind(this);
    this.handleArticleTrans = this.handleArticleTrans.bind(this);
  }

  componentDidMount() {
    this.timeoutId = setTimeout(() => {
        this.setState({loading: ''});
    }, 100);
  }

  componentWillUnmount() {
    if (this.timeoutId) {
        clearTimeout(this.timeoutId);
    }
  }

  handleOpenArticle(article, manuallyPushHistory=true) {

    // toggle background zooming effect
    this.setState({
      isArticleVisible: !this.state.isArticleVisible,
      article
    })

    // toggle component display property
    setTimeout(() => {
      this.setState({
        timeout: !this.state.timeout
      })
    }, 325)

    // trigger article display animation
    setTimeout(() => {
      this.setState({
        articleTimeout: !this.state.articleTimeout
      })
    }, 350)
  }

  handleCloseArticle() {

    // trigger article display animation
    this.setState({
      articleTimeout: !this.state.articleTimeout
    })

    // toggle component display property
    setTimeout(() => {
      this.setState({
        timeout: !this.state.timeout
      })
    }, 325)

    // toggle background zooming effect
    setTimeout(() => {
      this.setState({
        isArticleVisible: !this.state.isArticleVisible,
        article: ''
      })
    }, 350)
  }

  handleArticleTrans(article, manuallyPushHistory=true) {

    // trigger article display animation
    this.setState({
      articleTimeout: !this.state.articleTimeout
    });

    // set article
    setTimeout(() => {
      this.setState({
        article
      })
    }, 325);

    // trigger article display animation
    setTimeout(() => {
      this.setState({
        articleTimeout: !this.state.articleTimeout
      })
    }, 350);
  
  }

  handleMenuIn = () => {
    // set article display:none if not already set
    if(this.state.articleTimeout) {
      this.setState({
        articleTimeout: false
      });
    }

    // set menu and footer display:flex if not already set
    if(this.state.timeout) {
      this.setState({
        timeout: false
      })
    }

    // set background in-focus if not already set
    if(this.state.isArticleVisible) {
      setTimeout(() => {
        this.setState({
          isArticleVisible: false,
          article: ''
        })
      }, 25);
    }
  }

  handleMenuOut = () => {
    // set background out of focus
    this.setState({
      isArticleVisible: !this.state.isArticleVisible
    });

    // set menu and footer display:none
    setTimeout(() => {
      this.setState({
        timeout: false
      })
    }, 325);
  }

  handlePageIn = () => {
    let timer = 0;
    // set background out of focus if not already set
    if(!this.state.isArticleVisible) {
      this.setState({
        isArticleVisible: true
      })
    }

    // set menu and footer display:none if not already set
    if(this.state.timeout) {
      timer += 325;
      setTimeout(() => {
        this.setState({
          timeout: false
        })
      }, timer);
    }

    // set article display:flex
    timer += 150;
    setTimeout(() => {
      this.setState({
        articleTimeout: true
      })
    }, timer);
  }

  handlePageOut = () => {
    // set article display:none
    this.setState({
      articleTimeout: false
    });
  }

  navigateWithTimeout = (from, to) => {
    if(from === 'menu') {
      this.handlePageOut();
        setTimeout(() => {
          navigateTo(to);
      }, 200);
    } else if(from === 'page') {
      this.handlePageOut();
        setTimeout(() => {
          navigateTo(to);
      }, 200);
    }

    
  }

  render() {
    const siteTitle = this.props.data.site.siteMetadata.title
    const siteDescription = this.props.data.site.siteMetadata.description
    const { location, children } = this.props

    let rootPath = `/`

    let content;

    if (location.pathname === rootPath) {
      content = (
        <div id="wrapper">
          <Header 
            onOpenArticle={this.handleOpenArticle}
            onMenuIn={this.handleMenuIn}
            onMenuOut={this.handleMenuOut}
            timeout={this.state.timeout}
            navigateWithTimeout={this.navigateWithTimeout}
          />
          {/* <Main
            isArticleVisible={this.state.isArticleVisible}
            timeout={this.state.timeout}
            articleTimeout={this.state.articleTimeout}
            article={this.state.article}
            onCloseArticle={this.handleCloseArticle}
            onArticleTrans={this.handleArticleTrans}
            blogData={this.props.data.allMarkdownRemark.edges}
          /> */}
          <Footer timeout={this.state.timeout} />
        </div>
      )
    } else {
      content = (
        <div id="wrapper" className="page">
          <div id="main">
            {children({
              ...this.props,
              article: this.state.article,
              onOpenArticle: this.handleOpenArticle, 
              articleTimeout: this.state.articleTimeout,
              onCloseArticle: this.handleCloseArticle,
              blogData: this.props.data.allMarkdownRemark.edges,
              onArticleTrans: this.handleArticleTrans,
              onPageIn: this.handlePageIn,
              onPageOut: this.handlePageOut,
              navigateWithTimeout: this.navigateWithTimeout
            })}
          </div>
        </div>
      )
    }

    return (
      <div className={`body ${this.state.loading} ${this.state.isArticleVisible ? 'is-article-visible' : ''}`}>
        <Helmet>
            <title>{siteTitle}</title>
            <meta name="description" content={siteDescription} />
            <link key="icon" rel="icon" href={favicon} />
        </Helmet>

        {content}

        <div id="bg"></div>
      </div>
    )
  }
}

export default Template;

export const pageQuery = graphql`
  query PageQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
    allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          excerpt
          timeToRead
        }
      }
    }
  }
`;


