import React, { Component } from 'react';
import PropTypes from 'prop-types';
// import Link, { navigateTo } from 'gatsby-link';

class Header extends Component {
    componentDidMount() {
        console.log('menu mounted');
        this.props.onMenuIn();
    }
    componentWillUnmount() {
        console.log('menu is going out');
        // this.props.onMenuOut();
    }

    // navigateWithTimeout = (to) => {
    //     this.props.onMenuOut();
    //     setTimeout(() => {
    //         navigateTo(to);
    //     }, 325);
    // }

    render() {
        return (
            <header id="header" style={this.props.timeout ? {display: 'none'} : {}}>
                <div className="logo">
                    <span className="icon fa-terminal"></span>
                </div>
                <div className="content">
                    <div className="inner">
                        <h1>David's</h1>
                        <p>
                            Hi there, welcome to my site. I hope you find it interesting.<br /> Feel free to contact me if you want to know more about me or just want a chat!
                        </p>
                    </div>
                </div>
                <nav>
                    <ul>
                        {/* <li><a href="/#intro" onClick={() => {this.props.onOpenArticle('intro')}}>Intro</a></li>
                        <li><a href="/#work" onClick={() => {this.props.onOpenArticle('work')}}>Work</a></li>
                        <li><a href="/#about" onClick={() => {this.props.onOpenArticle('about')}}>About</a></li>
                        <li><a href="/#blog" onClick={() => {this.props.onOpenArticle('blog')}}>Blog</a></li>
                        <li><a href="/#contact" onClick={() => {this.props.onOpenArticle('contact')}}>Contact</a></li> */}
                        <li><a href="javascript:;" onClick={() => this.props.navigateWithTimeout("menu", "/intro")}>Intro</a></li>
                        <li><a href="javascript:;" onClick={() => this.props.navigateWithTimeout("menu", "/work")}>Work</a></li>
                        <li><a href="javascript:;" onClick={() => this.props.navigateWithTimeout("menu", "/about")}>About</a></li>
                        <li><a href="javascript:;" onClick={() => this.props.navigateWithTimeout("menu", "/blog")}>Blog</a></li>
                        <li><a href="javascript:;" onClick={() => this.props.navigateWithTimeout("menu", "/contact")}>Contact</a></li>
                        {/* <li><Link to="/intro">Intro</Link></li>
                        <li><Link to="/work">Work</Link></li>
                        <li><Link to="/about">About</Link></li>
                        <li><Link to="/blog">Blog</Link></li>
                        <li><Link to="/contact">Contact</Link></li> */}
                    </ul>
                </nav>
            </header>
        );
    }
}

Header.propTypes = {
    onOpenArticle: PropTypes.func,
    timeout: PropTypes.bool
}

export default Header;


